import { Box, Icon } from "@material-ui/core";
import { Link } from "@reach/router";
import React from "react";
import UseStyles from "../../config/UseStyles";

const WizardCard = (props: { to: string; children: any; icon?: any }) => {
  const classes = UseStyles();

  return (
    <Link to={props.to} className={classes.wizardCardLink}>
      <Box p={4}>
        <Box>
          {/* <span className="MuiIcon-root material-icons-two-tone MuiIcon-colorPrimary" style={{ fontSize: 50 }}>{props.icon}</span> */}
          <Icon
            // className="material-icons-two-tone"
            color="primary"
            style={{ fontSize: 50 }}
          >
            {props.icon}
          </Icon>
        </Box>
        <Box fontSize="h6.fontSize">{props.children}</Box>
      </Box>
    </Link>
  );
};

export default WizardCard;
