import React from "react";
import AgencyFactory from "../../factories/AgencyFactory";
import WorkerTwoYearFactory from "../../factories/WorkerTwoYearFactory";
import IWorkerRental from "../../interfaces/IWorkerRental";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import Faker from "faker";
import {
  TransformAgency,
  TransformTwoYearWorker,
} from "../../etl/MainTransformer";
import ISelectedSlotGroup from "../../interfaces/ISelectedSlotGroup";
import SelectedSlotGroupHourlyFactory from "../../factories/SelectedSlotGroupHourlyFactory";
import { demoRentalHourlyRemarks } from "../../config/DemoItems";
import IAgency from "../../interfaces/IAgency";

interface IAppContext {
  selectedWorker: null | IWorkerTwoYear | IWorkerRental;
  selectedWorkerRentalHourly: null | IWorkerRental;
  selectedWorkerRentalMonthly: null | IWorkerRental;
  setSelectedWorker: Function;
  selectedSlots: ISelectedSlotGroup[];
}

export const appContextDefault: IAppContext = {
  selectedWorker: defaultSelectedWorker(),
  selectedWorkerRentalHourly: defaultSelectedWorkerRentalHourly(),
  selectedWorkerRentalMonthly: defaultSelectedWorkerRentalMonthly(),
  selectedSlots: defaultSelectedSlots(),
  setSelectedWorker: () => {},
};

const AppContext = React.createContext<IAppContext>(appContextDefault);

function defaultSelectedWorker() {
  let agency = defaultAgency();
  let worker = WorkerTwoYearFactory.build({
    agency: agency,
  });
  TransformTwoYearWorker(worker, agency);

  return worker;
}

function defaultSelectedWorkerRentalHourly(): IWorkerRental | null {
  let agency = defaultAgency();

  if (agency.servicesSelectableWorkers) {
    let service = agency.servicesSelectableWorkers.find(
      (i) => i.isRentalHourly
    );
    if (service) {
      let worker = service.workersRental[0];
      worker.remarks = Faker.random.arrayElement(demoRentalHourlyRemarks);
      return worker;
    }
  }
  return null;
}

function defaultSelectedWorkerRentalMonthly(): IWorkerRental | null {
  let agency = defaultAgency();

  if (agency.servicesSelectableWorkers) {
    let service = agency.servicesSelectableWorkers.find(
      (i) => i.isRentalMonthly || i.isRentalMonthlyStayOver
    );
    if (service) {
      let worker = service.workersRental[0];
      worker.remarks = Faker.random.arrayElement(demoRentalHourlyRemarks);
      return worker;
    }
  }
  return null;
}

function defaultSelectedSlots(): ISelectedSlotGroup[] {
  let groups = SelectedSlotGroupHourlyFactory.buildList(2);
  return groups;
}

function defaultAgency(): IAgency {
  let agency = AgencyFactory.build();
  TransformAgency(agency, 999);
  return agency;
}

// function randomItem(array: any[]): any {
//   return array[Math.floor(Math.random() * array.length)];
// }

export default AppContext;
