import { Box } from "@material-ui/core";
import React from "react";
import "./GSign.css";

const SignInButton = (props: {
  text: string;
  icon: JSX.Element;
  buttonClass?: string;
}) => {
  return (
    <Box my={2}>
      <div id="my-signin2">
        <div
          style={{
            height: 50,

            margin: "auto",
          }}
          className={"abcRioButton abcRioButton" + props.buttonClass}
        >
          <div className="abcRioButtonContentWrapper">
            <div className="abcRioButtonIcon" style={{ padding: 15 }}>
              <div
                style={{ width: 18, height: 18 }}
                className="abcRioButtonSvgImageWithFallback abcRioButtonIconImage abcRioButtonIconImage18"
              >
                {props.icon}
              </div>
            </div>
            <span
              style={{ fontSize: 16, lineHeight: "48px" }}
              className="abcRioButtonContents"
            >
              <span id="not_signed_inw6llgzlp179g" style={{  fontWeight:"bold" }}>{props.text}</span>
              <span id="connectedw6llgzlp179g" style={{ display: "none" }}>
                Signed in with Google
              </span>
            </span>
          </div>
        </div>
      </div>
    </Box>
  );
};

export default SignInButton;
