import { RouteComponentProps } from "@reach/router";
import React, { Component } from "react";
import SideFilter from "../side-filter/SideFilter";
import { navigate } from "@reach/router";
import { Box, Button } from "@material-ui/core";
import Branding from "../../config/Branding";

interface Params {
  is2Year: boolean;
  isLeased: boolean;
}
interface Props extends RouteComponentProps<Params> {}
interface State {}

export default class FilterPage extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.back = this.back.bind(this);
  }

  state = {};

  back() {
    navigate(-1);
  }

  render() {
    const is2Year =
      this.props.is2Year !== undefined ? this.props.is2Year : false;
    const isLeased =
      this.props.isLeased !== undefined ? this.props.isLeased : false;

    return (
      <Box>
        <Box pb={7}>
          <SideFilter is2Year={is2Year} isLeased={isLeased} isMd={false} />
        </Box>
        <Box
          style={{
            position: "fixed",
            bottom: 0,
            left:0,
            right:0,
          }}
        >
          <Box bgcolor="#dddddddd" boxShadow={Branding.shadow.niceShadow}>
          <Box px={2} py={2}>
            <Button
              fullWidth={true}
              variant="contained"
              color="primary"
              onClick={this.back}
            >
              Apply All
            </Button>
          </Box>
          </Box>
        </Box>
      </Box>
    );
  }
}
