import { Button } from '@material-ui/core';
import { Link } from '@reach/router';
import React from 'react';

const SelectWorkerButton = () => {

  const linkStyles: React.CSSProperties = {
    display: "inline-flex",
    textDecoration: "none",
  };

  return (
    <Link to="/login" style={linkStyles}>
    <Button
      style={{ marginLeft: 8 }}
      size="small"
      color="primary"
      variant="outlined"
    >
      Select
    </Button>
  </Link>
  );
};

export default SelectWorkerButton;