import { Avatar, Box, Grid, Tab, Tabs, Typography } from "@material-ui/core";
import React from "react";
import IAgency from "../../interfaces/IAgency";
import AgencyRatingInfo from "./AgencyRatingInfo";
import NiceBox from "../common/NiceBox";
import AgencyTabPanels from "./AgencyTabPanels";
import IAgencyTab from "../../interfaces/IAgencyTab";

const AgencyDetails = (props: {
  agency: IAgency;
  showTwoYearTabs: boolean;
  showRentalTabs: boolean;
}) => {
  const [value, setValue] = React.useState(0);

  const handleTabChange = (event: React.ChangeEvent<{}>, newValue: any) => {
    setValue(newValue);
  };

  const agency = props.agency;

  let tabs: IAgencyTab[];

  if (props.showTwoYearTabs) {
    tabs = agency.tabsTwoYearWorkers;
  } else {
    // tabs = [...agency.tabsRental, ...agency.tabsServices];
    tabs = [...agency.tabsRental];
  }

  const tabNavigationButtons = tabs.map((tab) => (
    <Tab
      label={tab.title}
      style={{ fontWeight: "bold", textTransform: "none" }}
    />
  ));

  let tabsIndex = -1;
  const tabPanels = tabs.map((tab) => {
    tabsIndex++;
    return (
      <AgencyTabPanels
        value={value}
        tab={tab}
        index={tabsIndex}
        design="grid"
      />
    );
  });

  return (
    <NiceBox>
      <Grid container spacing={2}>
        <Grid item md={4} xs={12}>
          <Avatar
            variant="rounded"
            src={agency.logo}
            style={{
              flexShrink: 0,
              width: "100%",
              minHeight: 90,
            }}
          />
        </Grid>
        <Grid item md={8} xs={12}>
          <Typography variant="h5">{agency.title}</Typography>
          <Box>
            <AgencyRatingInfo agency={agency} />
            {/* <AgencyContactInfo agency={agency} /> */}
          </Box>
        </Grid>
        <Grid item xs={12}>
          <Box>
            <Tabs
              value={value}
              onChange={handleTabChange}
              indicatorColor="primary"
              scrollButtons="on"
              variant="scrollable"
            >
              {tabNavigationButtons}
            </Tabs>
          </Box>
          {tabPanels}
        </Grid>
      </Grid>
    </NiceBox>
  );
};

export default AgencyDetails;
