import AgencyCard from "../agency/AgencyCard";

import React, { Component } from "react";
import IAgenciesPage from "../../interfaces/IAgenciesPage";

class AgenciesPage extends Component<IAgenciesPage, any> {


  render() {

    const agencies = this.props.agencies.map((agency) => (
      <AgencyCard key={agency.id} agency={agency} showContractInfo={this.props.showContractInfo} showTabChips={this.props.showRentedInfo} />
    ));

    return <span>{agencies}</span>;
  }
}

export default AgenciesPage;
