import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  TextField,
  Typography,
} from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React from "react";
import Branding from "../../config/Branding";
import SmallText from "../common/SmallText";
import SignInWithApple from "../mock/SignInWithApple";
import SignInWithGoogle from "../mock/SignInWithGoogle";

interface ILoginPageParams {
  demoLink:string
}

interface ILoginPage extends RouteComponentProps<ILoginPageParams> {

}

const LoginPage = (props: ILoginPage) => {

  const demoLink = props.demoLink ? props.demoLink : "";

  return (
    <Box p={1} maxWidth={640} mx="auto">
      <Box
        my={2}
        py={3}
        borderRadius={Branding.borderRadius.niceRadius}
        boxShadow={Branding.shadow.niceShadow}
        bgcolor="white"
      >
        <Box maxWidth={346} mx="auto" px={2}>
          <Box textAlign="center">
            <Typography variant="h5">Login</Typography>
          </Box>
          <Link to={demoLink} style={{ textDecoration:"none" }}>
          <SignInWithGoogle />
          <SignInWithApple />
          </Link>
          <Box textAlign="center" p={1}>
            <Typography>OR</Typography>
          </Box>
          <Box>
            <form noValidate autoComplete="off">
              <TextField label="E-Mail" variant="outlined" />
              <TextField
                label="Password"
                type="password"
                autoComplete="current-password"
                variant="outlined"
              />
              <Box py={1}>
                <Grid container alignItems="center">
                  <Grid item xs={8}>
                    <Box>
                      <FormControlLabel
                        control={<Checkbox color="primary" size="small" />}
                        label={<SmallText>Keep Me Logged In</SmallText>}
                      />
                    </Box>
                  </Grid>
                  <Grid item xs={4}>
                    <Box textAlign="right">
                      <SmallText>
                        <Link to="/forgot-password">Forgot password?</Link>
                      </SmallText>
                    </Box>
                  </Grid>
                </Grid>
              </Box>
            </form>
            <Box py={1}>
              <Link to={demoLink}>
                <Button color="primary" variant="contained" fullWidth={true}>
                  LOGIN
                </Button>
              </Link>
            </Box>
          </Box>
          <Box mt={2} textAlign="center">
            <Typography>
              Don't have an account?
              <br /> <Link to="/signup">Click to sign-up</Link>
            </Typography>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default LoginPage;
