import { Chip } from "@material-ui/core";
import React from "react";
import Branding from "../../config/Branding";

const CardChips = (props: { titles: string[] }) => {
  const chips = props.titles.map((title) => (
    <Chip
      key={title}
      label={title}
      size="small"
      style={{
        margin: "4px 2px 4px 0",
        height: 18,
        fontSize: 10,
        borderRadius: Branding.borderRadius.niceRadius / 2,
      }}
    />
  ));
  return <React.Fragment>{chips}</React.Fragment>;
};

export default CardChips;
