import { Button } from "@material-ui/core";
import { Link } from "@reach/router";
import React from "react";

const ReserveWorkerButton = (props: { link?: string }) => {
  const link = props.link ? props.link : "/login";
  const linkStyles: React.CSSProperties = {
    display: "inline-flex",
    textDecoration: "none",
  };

  return (
    <Link to={link} style={linkStyles}>
      <Button
        style={{ marginLeft: 8 }}
        size="small"
        color="primary"
        variant="outlined"
      >
        Reserve
      </Button>
    </Link>
  );
};

export default ReserveWorkerButton;
