import * as Factory from "factory.ts";
import Faker from "faker";
import ISelectedSlot from "../interfaces/ISelectedSlot";

const SelectedSlotHourlyFactory = Factory.Sync.makeFactory<ISelectedSlot>({
  displayText: Factory.each(() =>
    Faker.random.arrayElement(["8:00 - 9:00 am", "4:30 - 6:30 pm"])
  ),
  feesBhd: Factory.each(() => Faker.random.number({ min: 4, max: 6 })),
});

export default SelectedSlotHourlyFactory;
