import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import { CancelOutlined, LocationOnOutlined } from '@material-ui/icons';
import { Box } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      padding: '2px 4px',
      display: 'flex',
      alignItems: 'center',
      maxWidth: 400,
    },
    input: {
      marginLeft: theme.spacing(1),
      flex: 1,
    },
    iconButton: {
      padding: 10,
    },
    divider: {
      height: 28,
      margin: 4,
    },
  }),
);

export default function WizardMapInputField() {
  const classes = useStyles();

  return (
    <Box border="1px solid #00000044" borderRadius={4} component="form" className={classes.root}>
      <IconButton className={classes.iconButton} aria-label="menu">
        <LocationOnOutlined />
      </IconButton>
      <InputBase
        className={classes.input}
        placeholder="My Address"
        inputProps={{ 'aria-label': 'My Address' }}
      />
      <IconButton type="submit" className={classes.iconButton} aria-label="search">
        <CancelOutlined />
      </IconButton>
      {/* <Divider className={classes.divider} orientation="vertical" />
      <IconButton color="primary" className={classes.iconButton} aria-label="directions">
        <DirectionsIcon />
      </IconButton> */}
    </Box>
  );
}