import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  Typography,
} from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React, { Component } from "react";
import Branding from "../../config/Branding";
import IPaymentSummaryItem from "../../interfaces/IPaymentSummaryItem";
import IWorkerRental from "../../interfaces/IWorkerRental";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import AppContext from "../common/AppContext";
import NiceBox from "../common/NiceBox";
import RequestAddress from "../request/RequestAddress";
import RequestFormGeneral from "../request/RequestFormGeneral";
import RequestFrequency from "../request/RequestFrequency";
import RequestPaymentSummary from "../request/RequestPaymentSummary";
import RequestWorkerRental from "../request/RequestWorkerRental";
import RequestWorkerTwoYear from "../request/RequestWorkerTwoYear";
import WorkerRentalCardSlots from "../worker/rental/WorkerRentalCardSlots";

interface IRequestWorkerPageParams {
  isTwoYear: boolean;
  isRentalHourly: boolean;
  isRentalMonthly: boolean;
  link: string;
}

interface IRequestWorkerPage
  extends RouteComponentProps<IRequestWorkerPageParams> {}

class RequestWorkerPage extends Component<IRequestWorkerPage, any, any> {
  static contextType = AppContext;

  render() {
    const isRentalHourly =
      this.props.isRentalHourly !== undefined
        ? this.props.isRentalHourly
        : false;
    const isRentalMonthly =
      this.props.isRentalMonthly !== undefined
        ? this.props.isRentalMonthly
        : false;
    const isRental = isRentalHourly || isRentalMonthly;

    let workerTwoYear: IWorkerTwoYear = this.context.selectedWorker;
    let workerRental: IWorkerRental = this.context.selectedWorkerRentalHourly;
    if (isRentalMonthly)
      workerRental = this.context.selectedWorkerRentalMonthly;
    let paymentItems: IPaymentSummaryItem[] = [
      {
        title: "Service Charge (Incl. VAT)",
        amount: 10.5,
      },
    ];

    const picWidth = "100%";
    const picHeight = 180;
    const borderColor = "#0000001f";
    const border = "1px solid " + borderColor;
    const headerVariant = "h6";
    const showRentalFormFields = this.props.isTwoYear ? false : true;

    if (isRentalHourly) {
      paymentItems = [
        {
          title: "Service Charges (Incl. VAT)",
          amount: 0.4,
        },
        {
          title: "Worker Charges",
          amount: 8,
        },
      ];
    }
    if (isRentalMonthly) {
      paymentItems = [
        {
          title: "Service Charges (Incl. VAT)",
          amount: 3.1,
        },

        {
          title: "Worker Charges",
          amount: 200,
        },
        {
          title: "Delivery Charges",
          amount: 25,
        },
      ];
    }

    const messageTwoYear =
      "Once the application has been sent, the agency will contact you within 1 business day.";

    const messageRental =
      "Currently we only accept payment as cash on delivery.";

    const selectScheduleLabel = isRentalHourly
      ? "Select Timing"
      : "Select Months";
    return (
      <NiceBox p={3}>
        <Grid container spacing={2} direction="row-reverse">
          <Grid item xs={12} md={7}>
            <Box>
              <Box pb={1}>
                <Typography variant={headerVariant}>
                  Selected Worker Details
                </Typography>
              </Box>
              {this.props.isTwoYear && (
                <RequestWorkerTwoYear
                  worker={workerTwoYear}
                  picHeight={picHeight}
                  picWidth={picWidth}
                />
              )}
              {isRental && (
                <>
                  <RequestWorkerRental
                    worker={workerRental}
                    picHeight={picHeight}
                    picWidth={picWidth}
                  />
                  {false && <RequestAddress />}
                </>
              )}
            </Box>
          </Grid>
          <Grid item xs={12} md={5}>
            <Box pr={2} borderRight={border}>
              {isRental && (
                <>
                  <Box pb={1}>
                    <Typography variant={headerVariant}>
                      {selectScheduleLabel}
                    </Typography>
                  </Box>
                  <WorkerRentalCardSlots
                    isHourly={isRentalHourly}
                    isMonthly={isRentalMonthly}
                    worker={workerRental}
                    md={3}
                    maxDays={8}
                    maxMonths={12}
                  />
                  <Box py={1}>
                    {isRentalHourly && (
                      <React.Fragment>
                        <RequestFrequency />
                        <FormControlLabel
                          label="Bring Equipment (2 BD/Day)"
                          control={<Checkbox color="primary" />}
                        />
                      </React.Fragment>
                    )}
                  </Box>
                </>
              )}
              <RequestFormGeneral
                showFrequency={showRentalFormFields}
                showAddress={showRentalFormFields}
                showEquipmentOption={false}
              />
              <RequestPaymentSummary paymentItems={paymentItems} />
              <Box
                py={2}
                fontStyle="italic"
                color={Branding.text.warning}
                fontWeight="bold"
              >
                {this.props.isTwoYear && messageTwoYear}
                {(this.props.isRentalHourly || this.props.isRentalMonthly) &&
                  messageRental}
              </Box>
              <Box textAlign="center" pb={6} pt={2}>
                {this.props.link && (
                  <Link to={this.props.link}>
                    <Button size="large" variant="contained" color="primary">
                      Send Request
                    </Button>
                  </Link>
                )}
              </Box>
            </Box>
          </Grid>
        </Grid>
      </NiceBox>
    );
  }
}

export default RequestWorkerPage;
