import { nationalities } from "../../config/FilterOptions";
import { IFilterGroupContainer } from "../../interfaces/IFilterGroupContainer";
import FilterCheckboxItem from "./FilterCheckboxItem";
import FilterGroup from "./FilterGroup";

const FGNationalities = (props:IFilterGroupContainer) => {
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title="Nationalities" expanded={expanded} hideDivider={props.hideDivider}>
      {nationalities.map((title) => (
        <FilterCheckboxItem key={title} title={title} />
      ))}
    </FilterGroup>
  );
};

export default FGNationalities;
