import { Box, Container, Grid } from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React from "react";
import Branding from "../../config/Branding";
import Logo from "../../images/logo.svg";
import Landing1 from "../../images/landing_bg1.jpg";

const SiteHeadLarge = (props: RouteComponentProps) => {
  return (
    <Box
      bgcolor={Branding.primary}
      style={{
        backgroundImage: `url(${Landing1})`,
        backgroundSize: "cover",
      }}
    >
      <Container>
        <Box height={230}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Box py={3}>
              <Link
                  to="/"
                  style={{
                    textDecoration: "none",
                    display: "inline-block",
                    verticalAlign: "top",
                  }}
                >
                <img
                  src={Logo}
                  style={{
                    display: "block",
                    maxHeight: 80,
                    maxWidth: "100%",
                  }}
                  alt="logo"
                />
                </Link>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </Box>
  );
};

export default SiteHeadLarge;
