import { Box, Grid } from "@material-ui/core";
import { Router } from "@reach/router";
import React, { Component } from "react";
import AgencyFactory from "../../factories/AgencyFactory";
import IPage from "../../interfaces/IPage";
import SideFilterContainer from "../side-filter/SideFilterContainer";
import AgenciesPage from "../pages/AgenciesPage";
import AgencyPage from "../pages/AgencyPage";
import MainTransformer from "../../etl/MainTransformer";
import AppContext, { appContextDefault } from "../common/AppContext";
import { demoAgencies } from "../../config/DemoItems";
import IAgency from "../../interfaces/IAgency";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import WorkerDrawerPage from "../pages/WorkerDrawerPage";
import _ from "lodash";
import { withBreakpoints } from "react-breakpoints";
import SideFilterHidden from "../side-filter/SideFilterHidden";

class AgenciesDataProvider extends Component<
  IPage,
  {
    agencies: IAgency[];
    agenciesContract: IAgency[];
    agenciesRented: IAgency[];
    workersTwoYears: IWorkerTwoYear[];
  }
> {
  constructor(props: IPage) {
    super(props);

    this.state = {
      agencies: [],
      agenciesContract: [],
      agenciesRented: [],
      workersTwoYears: [],
    };
  }

  componentDidMount() {
    
    
    let agencies = AgencyFactory.buildList(demoAgencies.length);
    let workersTwoYear: IWorkerTwoYear[] = [];
    MainTransformer(agencies);
    console.table(agencies);

    let agenciesContract = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasTwoYearWorkerServices;
      }),
      ["rating"],
      ["desc"]
    );

    let agenciesRented = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasRentedServices;
      }),
      ["rating"],
      ["desc"]
    );

    agencies.forEach((agency) => {
      if (agency.workersTwoYear) {
        workersTwoYear = workersTwoYear.concat(agency.workersTwoYear);
      }
    });

    this.setState({
      agencies: agencies,
      agenciesContract: agenciesContract,
      agenciesRented: agenciesRented,
      workersTwoYears: workersTwoYear,
    });
  }

  render() {
    const { breakpoints, currentBreakpoint } = this.props;
    const isMd = breakpoints[currentBreakpoint] >= breakpoints.md;
    const toRender = this.state.agencies ? (
      <Router primary={false}>
        <AgenciesPage
          agencies={this.state.agenciesContract}
          path="contract"
          showContractInfo={true}
        />
        <AgenciesPage
          agencies={this.state.agenciesRented}
          path="leased"
          showRentedInfo={true}
        />
        <AgencyPage
          agencies={this.state.agencies}
          showTwoYearTabs={true}
          path="contract/:agencyId"
        />
        <AgencyPage
          agencies={this.state.agencies}
          showRentalTabs={true}
          path="leased/:agencyId"
        />
        <WorkerDrawerPage
          workersTwoYear={this.state.workersTwoYears}
          path="contract/:agencyId/worker-details/:workerTwoYearId"
        />
      </Router>
    ) : null;

    return (
      <AppContext.Provider value={appContextDefault}>
        <Grid container spacing={0}>
          <Grid item xs={12} md={3}>
            <Box pr={isMd? 2 : 0}>
              <Router>
                <SideFilterHidden path="contract/:agencyId/worker-details/:workerTwoYearId"/>
                <SideFilterContainer default is2Year={true} isLeased={false} isMd={isMd} />
                <SideFilterContainer
                  path="leased/*"
                  is2Year={false}
                  isLeased={true}
                  isMd={isMd}
                />
              </Router>
            </Box>
          </Grid>
          <Grid item xs={12} md={9}>
            {toRender}
          </Grid>
        </Grid>
      </AppContext.Provider>
    );
  }
}

export default withBreakpoints(AgenciesDataProvider);
