import { Checkbox, FormControlLabel } from "@material-ui/core";
import React, { Component } from "react";
import SmallText from "../common/SmallText";

class FilterCheckboxItem extends Component<
  {
    title: string;
  },
  any
> {
  render() {
    return (
      <FormControlLabel
        control={
          <Checkbox color="primary" size="small" style={{ padding: 4, marginLeft: 16 }} />
        }
        label={<SmallText>{this.props.title}</SmallText>}
      />
    );
  }
}

export default FilterCheckboxItem;
