import * as Factory from "factory.ts";
import Faker from "faker";
import ISelectedSlotGroup from "../interfaces/ISelectedSlotGroup";
import SelectedSlotHourlyFactory from "./SelectedSlotHourlyFactory";
import dateFormat from "dateformat";

const SelectedSlotGroupHourlyFactory = Factory.Sync.makeFactory<ISelectedSlotGroup>(
  {
    displayText: Factory.each(() => dateFormat(Faker.date.recent(6),"ddd d-mmm")),
    slots: Factory.each(() =>
      SelectedSlotHourlyFactory.buildList(
        Faker.random.arrayElement([1, 1, 1, 2])
      )
    ),
  }
);

export default SelectedSlotGroupHourlyFactory;
