import { Box } from "@material-ui/core";
import { HomeTwoTone } from "@material-ui/icons";
import Branding from "../../config/Branding";
import IAgencyTab from "../../interfaces/IAgencyTab";
import { Design } from "../../types/Design";
import SmallText from "../common/SmallText";
import TabPanel from "../common/TabPanel";
import WizardDatePicker from "../wizard/controls/WizardDatePicker";
import WizardMonthMultiplePicker from "../wizard/controls/WizardMonthMultiplePicker";
import AgencyTabPanelGridDesign from "./AgencyTabPanelGridDesign";
import AgencyTabPanelListDesign from "./AgencyTabPanelListDesign";

const AgencyTabPanels = (props: {
  value: number;
  tab: IAgencyTab;
  index: number;
  design: Design;
}) => {
  const tab = props.tab;
  const isHourly = tab.rentalService?.isRentalHourly
    ? tab.rentalService.isRentalHourly
    : false;
  let isMonthly = false;

  if (
    tab.rentalService?.isRentalMonthly ||
    tab.rentalService?.isRentalMonthlyStayOver
  ) {
    isMonthly = true;
  }

  const iconStyles: React.CSSProperties = {
    fontSize: 19,
    color: Branding.primary,
    verticalAlign: "text-bottom",
  };
  return (
    <TabPanel
      key={tab.index + "_" + tab.title}
      value={props.value}
      index={props.index}
      pt={2}
    >
      {isMonthly && (
        <Box my={1}>
          <WizardMonthMultiplePicker />
        </Box>
      )}
      {isHourly && (
        <Box my={2}>
          <WizardDatePicker />
        </Box>
      )}
      {tab.remarks && (
        <Box
          boxShadow={Branding.shadow.niceShadow}
          p={1}
          my={1}
          bgcolor={Branding.lightest}
          borderRadius={Branding.borderRadius.niceRadius}
        >
          <SmallText>
            <HomeTwoTone style={iconStyles} /> {tab.remarks}
          </SmallText>
        </Box>
      )}
      {props.design === "grid" && (
        <AgencyTabPanelGridDesign
          tab={tab}
          isHourly={isHourly}
          isMonthly={isMonthly}
        />
      )}
      {props.design === "list" && (
        <AgencyTabPanelListDesign
          tab={tab}
          isHourly={isHourly}
          isMonthly={isMonthly}
        />
      )}
    </TabPanel>
  );
};

export default AgencyTabPanels;
