import {  Box, Chip, Grid, Icon } from "@material-ui/core";
import IWorkerTwoYear from "../../../interfaces/IWorkerTwoYear";
import DetailsGroup from "../DetailsGroup";
import LabelValueLine from "../../common/LabelValueLine";
import LabelValuesContainer from "../../common/LabelValuesContainer";
import WorkerTwoYearDetailsTopSection from "./Worker2TopSection";
import UseStyles from "../../../config/UseStyles";

const Worker2Details = (props: { worker: IWorkerTwoYear }) => {
  const worker = props.worker;
  const classes = UseStyles();
  const languageSkills = worker.languageSkills.map((skill) => (
    <LabelValueLine oneLine={true} key={skill.language} title={skill.language}>
      {skill.levelText}
    </LabelValueLine>
  ));

  const jobExperiences = worker.jobExperiences.map((job) => (
    <LabelValueLine
      oneLine={true}
      key={job.country + job.totalYears}
      title={job.country}
    >
      {job.fromYear}-{job.toYear}, {job.totalYears} Years
    </LabelValueLine>
  ));

  const otherSkills = worker.otherSkills.map((skill) => (
    <li>
      <Chip
        icon={<Icon fontSize="small">check</Icon>}
        size="small"
        color="primary"
        label={skill}
        key={skill}
        style={{ margin: 2 }}
      />
    </li>
  ));

  const picMinHeight = 500;

  return (
    <Box p={2} maxWidth={1100} className={classes.objectFitContain}>
      <WorkerTwoYearDetailsTopSection worker={worker} showButtons={true} />
      <Grid spacing={1} container direction="row-reverse">
        <Grid item xs={12} md={6}>
          <DetailsGroup>
            {/* <Avatar
              src={worker.bigPicture}
              variant="rounded"
              style={{
                width: "100%",
                minHeight: picMinHeight,
              }}
            /> */}
            <div
              className="MuiAvatar-root MuiAvatar-rounded"
              style={{
                width: "100%",
                minHeight: picMinHeight,
              }}
              
            >
              <img src={worker.bigPicture} alt={worker.name} style={{
                objectFit:"contain",
                height:"100%",
              }}/>
            </div>
          </DetailsGroup>
        </Grid>
        <Grid item xs={12} md={6}>
          <DetailsGroup title="Personal Details">
            <LabelValuesContainer>
              <LabelValueLine title="Nationality">
                {worker.nationality.title}
              </LabelValueLine>
              <LabelValueLine title="Religion">
                {worker.religion}
              </LabelValueLine>
              <LabelValueLine title="Date of Birth">
                {worker.dateOfBirth.toDateString()}
              </LabelValueLine>
              <LabelValueLine title="Age">
                {worker.age.toString()}
              </LabelValueLine>
              <LabelValueLine title="Place of Birth">
                {worker.cityOfBirth}
              </LabelValueLine>
              <LabelValueLine title="Living Town">
                {worker.addressOriginalCountry.city}
              </LabelValueLine>
              <LabelValueLine title="Marital Status">
                {worker.maritalStatus}
              </LabelValueLine>
              <LabelValueLine title="Number of Children">
                {worker.numberOfChildren}
              </LabelValueLine>
              <LabelValueLine title="Weight">
                {worker.weightKg} kg
              </LabelValueLine>
              <LabelValueLine title="Height">
                {worker.heightCm} cm
              </LabelValueLine>
              <LabelValueLine title="Complexion">
                {worker.complexion}
              </LabelValueLine>
              <LabelValueLine title="Educational Level">
                {worker.educationalLevel}
              </LabelValueLine>
            </LabelValuesContainer>
          </DetailsGroup>
          <DetailsGroup title="Language Skills">
            <LabelValuesContainer>{languageSkills}</LabelValuesContainer>
          </DetailsGroup>
          <DetailsGroup
            title="Work Experience"
            titleChip={worker.experienceYears.toString() + " Years"}
          >
            <LabelValuesContainer>{jobExperiences}</LabelValuesContainer>
          </DetailsGroup>
          <DetailsGroup title="Skills">
            <Box
              component="ul"
              display="flex"
              justifyContent="left"
              flexWrap="wrap"
              p={0}
              m={0}
              style={{
                listStyle: "none",
              }}
            >
              {otherSkills}
            </Box>
          </DetailsGroup>
          <DetailsGroup title="Remarks">{worker.remarks}</DetailsGroup>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Worker2Details;
