import { Box, Slider } from "@material-ui/core";
import React from "react";

const FilterSlider = (props: {
  min: number;
  max: number;
  defaultValue: number | number[];
  step: number;
}) => {
  return (
    <Box px={2}>
      <Slider
        defaultValue={props.defaultValue}
        valueLabelDisplay="auto"
        min={props.min}
        max={props.max}
        step={props.step}
        marks={[
          {
            value: props.min,
            label: props.min,
          },
          {
            value: props.max,
            label: props.max,
          },
        ]}
      />
    </Box>
  );
};

export default FilterSlider;
