import * as Factory from "factory.ts";
import IAgency from "../interfaces/IAgency";
import Faker from "faker";
import ReviewFactory from "./ReviewFactory";
import IAgencyServiceSelectableWorkers from "../interfaces/IAgencyServiceSelectableWorkers";
import AgencyServiceSelectableWorkersFactory from "./AgencyServiceSelectableWorkersFactory";
import AgencyServiceFactory from "./AgencyServiceFactory";
import NationalityFactory from "./NationalityFactory";
import { rentalServicesDetailed } from "../config/FilterOptions";

// Faker.locale = "ar";

let contractServices: IAgencyServiceSelectableWorkers[] = [];
let rentalServices: IAgencyServiceSelectableWorkers[] = [];

const contractServicesPart: Factory.RecPartial<
  IAgencyServiceSelectableWorkers[]
> = [
  {
    title: "Two Year Contract",
    isTwoYearContract: true,
    feesBhd: undefined,
  },
];

contractServicesPart.forEach((part) => {
  contractServices.push(AgencyServiceSelectableWorkersFactory.build(part));
});

const rentalServicesPart: Factory.RecPartial<
  IAgencyServiceSelectableWorkers[]
> = rentalServicesDetailed;

rentalServicesPart.forEach((part) => {
  rentalServices.push(AgencyServiceSelectableWorkersFactory.build(part));
});

const AgencyFactory = Factory.Sync.makeFactory<IAgency>({
  id: Factory.each((i) => i + 1),
  demoId: 0,
  // logo: Factory.each(
  //   () =>
  //     "http://placeimg.com/" +
  //     Faker.random.number({ min: 135, max: 140 }) +
  //     "/" +
  //     Faker.random.number({ min: 100, max: 105 }) +
  //     "/arch"
  // ),
  logo: Factory.each((i) => ""),
  // title: Factory.each(() => Faker.company.companyName()),
  title: Factory.each((i) => ""),
  new: Factory.each(() =>
    Faker.random.arrayElement([true, false, false, false, false])
  ),
  // reviews: Factory.each(() =>
  //   Faker.random.arrayElements<number>(
  //     [1],
  //     Faker.random.number({ min: 30, max: 4500 })
  //   )
  // ),
  reviews: Factory.each(() =>
    ReviewFactory.buildList(Faker.random.number({ min: 7, max: 35 }))
  ),

  rating: Factory.each(() => Faker.random.float({ min: 0, max: 5 })),
  ratingHtml: "😃 Amazing",
  // address: Factory.each(() => Faker.address.city()),
  email: Factory.each(() => Faker.internet.email()),
  telephone: Factory.each(
    () => "+973 3" + Faker.random.number({ min: 1000000, max: 9999999 })
  ),
  fax: Factory.each(() => Faker.phone.phoneNumber()),
  servicesSelectableWorkers: Factory.each(() =>
    Faker.random.arrayElements<IAgencyServiceSelectableWorkers>(
      [...rentalServices, ...contractServices],
      Faker.random.number({ min: 2, max: 2 })
    )
  ),

  services: Factory.each(() =>
    AgencyServiceFactory.buildList(Faker.random.number({ min: 0, max: 3 }))
  ),

  hasRentedServices: false,
  hasTwoYearWorkerServices: false,

  nationalities: Factory.each(() =>
    NationalityFactory.buildList(Faker.random.number({ min: 1, max: 4 }))
  ),

  // workersTwoYear: Factory.each(() =>
  //   WorkerTwoYearFactory.buildList(Faker.random.number({ min: 4, max: 10 }))
  // ),

  tabs: [],
  tabsRental: [],
  tabsServices: [],
  tabsTwoYearWorkers: [],

  // demoWorkers: Factory.each(() => ({
  //   philippines: WorkerTwoYearFactory.buildList(
  //     Faker.random.number({ min: 2, max: 5 }),
  //     {
  //       nationality: { title: "Philippines" },
  //     }
  //   ),
  //   india: WorkerTwoYearFactory.buildList(
  //     Faker.random.number({ min: 2, max: 5 }),
  //     {
  //       nationality: { title: "India" },
  //     }
  //   ),
  // })),
  hourlySlotsGenerateDaysInAdvance: 12,
});

export default AgencyFactory;
