import React from 'react';
import { maxAge, minAge } from '../../config/FilterOptions';
import { IFilterGroupContainer } from '../../interfaces/IFilterGroupContainer';
import FilterGroup from './FilterGroup';
import FilterSlider from './FilterSlider';

const FGAge = (props:IFilterGroupContainer) => {
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title="Age" expanded={expanded}>
    <FilterSlider
        defaultValue={[20, 45]}
        min={minAge}
        max={maxAge}
        step={1}
      />
    </FilterGroup>
  );
};

export default FGAge;