import * as Factory from "factory.ts";
import Faker from "faker";
import IHourlySlotDefinition from "../interfaces/IHourlySlotDefinition";

const HourlySlotDefinitionFactory = Factory.Sync.makeFactory<IHourlySlotDefinition>(
  {
    id: Factory.each((i) => i + 1),
    fromString: Factory.each(() =>
      Faker.random.arrayElement([
        "06:00",
        "08:30",
        "11:00",
        "13:30",
        "16:00",
        "18:30",
      ])
    ),
    hours: Factory.each(() => Faker.random.number({ min: 3, max: 5 }) / 2),
    toString: "",
  }
);

export default HourlySlotDefinitionFactory;
