import React from 'react';
import { maxProcessingFees, minProcessingFees } from '../../config/FilterOptions';
import { IFilterGroupContainer } from '../../interfaces/IFilterGroupContainer';
import FilterGroup from './FilterGroup';
import FilterSlider from './FilterSlider';

const FGProcessFees = (props:IFilterGroupContainer) => {
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title="Process Fees (BD)" expanded={expanded}>
    <FilterSlider
      defaultValue={[minProcessingFees, 900]}
      min={minProcessingFees}
      max={maxProcessingFees}
      step={50}
    />
  </FilterGroup>
  );
};

export default FGProcessFees;