import * as Factory from "factory.ts";
import Faker from "faker";
import { jobTitles, nationalities } from "../config/FilterOptions";
import IAgencyService from "../interfaces/IAgencyService";

const AgencyServiceFactory = Factory.Sync.makeFactory<IAgencyService>({
  title: Factory.each(() =>
    Faker.random.arrayElement([
      "Worker for " +
        Faker.random.number({ min: 4, max: 7 }) +
        " days per week",
    ])
  ),
  description: Factory.each(() =>
    Faker.random.arrayElement([
      "Available for " +
        Faker.random.number({ min: 4, max: 7 }) +
        " days per week with delivery, minimum hours is " +
        Faker.random.number({ min: 2, max: 4 }) +
        " hours. " +
        Faker.random.arrayElement(nationalities) +
        " " +
        Faker.random.arrayElement(jobTitles) +
        ".",
    ])
  ),
  isRentalHourly: false,
  isRentalMonthly: false,
  isRentalMonthlyStayOver: false,
  isTwoYearContract: false,
  feesText: Factory.each(() =>
    Faker.random.arrayElement([
      "2 BD / Hr. Delivery 2 BD per visit",
      "2 BD / Hr. Free delivery",
      "3 BD / Hr",
      "15 BD / Wk. Free Delivery",
      "400 BD / Mo",
    ])
  ),
});

export default AgencyServiceFactory;
