import { Drawer } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "../../../interfaces/IWorkerTwoYear";
import { Anchor } from "../../../types/Anchor";
import { Design } from "../../../types/Design";
import Worker2Details from "./Worker2Details";
import { withBreakpoints } from "react-breakpoints";
import { useNavigate } from "@reach/router";
import Worker2ListCard from "./Worker2ListCard";
import Worker2GridCard from "./Worker2GridCard";
import { ClickEvent } from "../../../types/ClickEvent";

const Worker2CardWrapper = (props: {
  worker: IWorkerTwoYear;
  design: Design;
  breakpoints: any;
  currentBreakpoint: any;
}) => {
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const navigate = useNavigate();

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent
  ) => {
    if (
      event.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" ||
        (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const linkToDrawerPage = () => (
    event: React.KeyboardEvent | React.MouseEvent
  ) => {
    if (props.worker.agency) {
      navigate(props.worker.agency.id + "/worker-details/" + props.worker.id);
    }
  };

  const { breakpoints, currentBreakpoint } = props;

  const anchor: Anchor = "right";

  let picClickEvent: ClickEvent;

  if (breakpoints[currentBreakpoint] > breakpoints.md) {
    picClickEvent = toggleDrawer(anchor, true);
  } else {
    picClickEvent = linkToDrawerPage();
  }

  return (
    <React.Fragment key={props.worker.id}>
      {props.design === "list" && (
        <Worker2ListCard worker={props.worker} picClickEvent={picClickEvent} />
      )}
      {props.design === "grid" && (
        <Worker2GridCard worker={props.worker} picClickEvent={picClickEvent} />
      )}
      <Drawer
        anchor={anchor}
        open={state[anchor]}
        onClose={toggleDrawer(anchor, false)}
      >
        <Worker2Details worker={props.worker} />
      </Drawer>
    </React.Fragment>
  );
};

export default withBreakpoints(Worker2CardWrapper);
