import FormatCurrency from "../helpers/FormatCurrency";
import IAgency from "../interfaces/IAgency";
import IWorkerTwoYear from "../interfaces/IWorkerTwoYear";
import _ from "lodash";
import IAgencyTab from "../interfaces/IAgencyTab";
import WorkerTwoYearFactory from "../factories/WorkerTwoYearFactory";
import Faker from "faker";
import INationality from "../interfaces/INationality";
import IWorkerRental from "../interfaces/IWorkerRental";
import IAgencyServiceSelectableWorkers from "../interfaces/IAgencyServiceSelectableWorkers";
import { maxSalary, minSalary } from "../config/FilterOptions";
import TimeToDate, { AddToTime } from "../helpers/TimeToDate";
import { MakeHourlySlots, MakeMonthlySlots } from "./SlotsTransformer";
import {
  demoAgencies,
  demoItemsCount,
  demoRentalMonthlyRemarks,
  demoRentalHourlyRemarks,
  demoRentalMonthlyStayOverRemarks,
  demoNumberOfContractWorkersPerAgency,
} from "../config/DemoItems";
import { MakeHourlySlotsDictionary } from "./SlotsGrouper";

export default function MainTransformer(agencies: IAgency[]) {
  let i = 1;
  agencies.forEach((agency) => {
    TransformAgency(agency, i);
    i++;
  });
}

export function TransformAgency(agency: IAgency, index: number) {
  // agency.ratingHtml = TransformRatingText(agency.rating);
  let tabsIndex = 0;

  if (agency.demoId === 0) {
    agency.demoId = index;
    let demoAgency = demoAgencies.find((x) => x.id === agency.demoId);
    if (demoAgency) {
      agency.logo = "/img/agencies/" + agency.demoId + ".png";
      agency.title = demoAgency.name;
    } else {
      agency.title = Faker.company.companyName();
    }
  }

  agency.showReviewsRatings = agency.rating >= 3;

  if (agency.servicesSelectableWorkers) {
    agency.servicesSelectableWorkers.forEach((service) => {
      if (service.isTwoYearContract) {
        agency.hasTwoYearWorkerServices = true;
      }
      if (
        service.isRentalHourly ||
        service.isRentalMonthly ||
        service.isRentalMonthlyStayOver
      ) {
        agency.hasRentedServices = true;
        service.workersRental = _.orderBy(
          service.workersRental,
          ["rating"],
          ["desc"]
        );
      }
    });
  }

  agency.nationalities?.forEach((nationality) =>
    TransformNationalities(agency, nationality)
  );

  if (agency.hasTwoYearWorkerServices) {
    agency.workersTwoYear = WorkerTwoYearFactory.buildList(
      Faker.random.number(demoNumberOfContractWorkersPerAgency)
    );
    agency.workersTwoYear.forEach((worker) => {
      TransformTwoYearWorker(worker, agency);
    });
    tabsIndex = MakeTabsTwoYearWorkers(agency, tabsIndex);
  }

  if (agency.hasRentedServices) {
    agency.servicesSelectableWorkers?.forEach((service) => {
      service.workersRental.forEach((worker) => {
        TransformRentalWorker(worker, service, agency);
      });
    });
    tabsIndex = MakeTabsRentalWorkers(agency, tabsIndex);
  }

  if (agency.services) {
    MakeTabsServices(agency, tabsIndex);
  }
}

export function TransformTwoYearWorker(
  worker: IWorkerTwoYear,
  agency: IAgency
) {
  // if(worker.maritalStatus === "Single")
  //   worker.numberOfChildren = 0;
  let grandTotalYears = 0;
  worker.agency = agency;
  worker.jobExperiences.forEach((job) => {
    job.toYear = job.fromYear + job.totalYears;
    grandTotalYears += job.totalYears;
  });
  worker.experienceYears = grandTotalYears;
  worker.monthlySalaryBhdFormatted =
    FormatCurrency(worker.monthlySalaryBhd, 0) + " BD / Month";
  worker.processFeesBhdFormatted =
    FormatCurrency(worker.processFeesBhd, 0) + " BD";

  if (agency.nationalities) {
    worker.nationality.title = Faker.random.arrayElement(
      agency.nationalities
    ).title;
    let demoId = getDemoId(worker.nationality.title);
    worker.smallPicture = getDemoPic(demoId, worker.nationality.title, "Small");
    worker.bigPicture = getDemoPic(demoId, worker.nationality.title, "Full");
  }
}

export function TransformRentalWorker(
  worker: IWorkerRental,
  service: IAgencyServiceSelectableWorkers,
  agency: IAgency
) {
  let feesBhd: string | undefined;
  worker.service = service;
  worker.agency = agency;
  // worker.feesText = service.feesText ? service.feesText : "";

  if (service.isRentalHourly) {
    worker.remarks = Faker.random.arrayElement(demoRentalHourlyRemarks);
    feesBhd = Faker.random.number({ min: 2, max: 3 }).toString();
    worker.hourlySlots = MakeHourlySlots(worker);
    worker.hourlySlotsDictionary = MakeHourlySlotsDictionary(
      worker.hourlySlots
    );
  }

  if (service.isRentalMonthly) {
    worker.remarks = Faker.random.arrayElement(demoRentalMonthlyRemarks);
    worker.monthlySlots = MakeMonthlySlots(worker);
  }

  if (service.isRentalMonthlyStayOver) {
    worker.remarks = Faker.random.arrayElement(
      demoRentalMonthlyStayOverRemarks
    );
    worker.monthlySlots = MakeMonthlySlots(worker);
  }

  worker.hourlySlotDefinitions.forEach((definition) => {
    definition.from = TimeToDate(definition.fromString);
    definition.toString = AddToTime(definition.fromString, definition.hours);
    definition.to = TimeToDate(definition.toString);
  });

  if (service.isRentalMonthly || service.isRentalMonthlyStayOver) {
    const step = 20;
    feesBhd = (
      Faker.random.number({ min: minSalary / step, max: maxSalary / step }) *
      step
    ).toString();
  }

  if (service.feesBhdUnit) {
    worker.feesText = feesBhd + " BD/" + service.feesBhdUnit;
  }

  let demoId = getDemoId(worker.nationality.title);
  worker.smallPicture = getDemoPic(demoId, worker.nationality.title, "Small");
}

function getDemoId(nationality: string): number {
  let demoItem = demoItemsCount.find((x) => x.name === nationality);
  if (demoItem) {
    return Faker.random.number({ min: 1, max: demoItem.count });
  }
  return 0;
}

function getDemoPic(
  id: number,
  nationality: string,
  size: "Small" | "Full"
): string {
  return "/img/" + nationality + "/" + id + "_" + size + ".png";
}

