function MakeTabsTwoYearWorkers(agency: IAgency, index: number): number {
  if (agency.workersTwoYear) {
    let workers = agency.workersTwoYear;
    let groupedWorkers = _.groupBy(workers, "nationality.title");

    _.forEach(groupedWorkers, (value, key) => {
      let feesText = "";
      let titleWithFees = key;
      if (value[0].processFeesBhdFormatted) {
        feesText = value[0].processFeesBhdFormatted;
        titleWithFees += " (" + feesText + ")";
      }

      let tab: IAgencyTab = {
        index: index,
        title: key,
        titleWithFees: titleWithFees,
        feesText: feesText,
        isTwoYearContract: true,
        isRented: false,
        isServices: false,
        workersTwoYear: value,
      };
      index++;
      agency.tabs.push(tab);
      agency.tabsTwoYearWorkers.push(tab);
    });
  }
  return index;
}

function MakeTabsRentalWorkers(agency: IAgency, index: number): number {
  agency.servicesSelectableWorkers?.forEach((service) => {
    if (
      service.isRentalHourly ||
      service.isRentalMonthly ||
      service.isRentalMonthlyStayOver
    ) {
      let feesText = service.feesText ? service.feesText : "";
      let tab: IAgencyTab = {
        index: index,
        title: service.title,
        feesText: feesText,
        titleWithFees: service.title + " " + feesText,
        isRented: true,
        isTwoYearContract: false,
        isServices: false,
        workersRental: service.workersRental,
        rentalService: service,
        remarks: service.remarks,
      };
      index++;
      agency.tabs.push(tab);
      agency.tabsRental.push(tab);
    }
  });

  return index;
}

function MakeTabsServices(agency: IAgency, index: number): number {
  let title = "Packages";

  let tab: IAgencyTab = {
    index: index,
    title: title,
    isRented: false,
    isServices: true,
    isTwoYearContract: false,
    feesText: "",
    titleWithFees: title,
    services: agency.services,
  };
  agency.tabs.push(tab);
  agency.tabsServices.push(tab);
  index++;

  return index;
}

function TransformNationalities(agency: IAgency, nationality: INationality) {
  if (nationality.titleWithFees === undefined) {
    let fees: number;
    let feesUnit: string;

    if (agency.hasRentedServices) {
      fees = Faker.random.number({
        min: 2,
        max: 5,
      });
      feesUnit = "BD/Hr";
    } else {
      fees = 1100;
      feesUnit = "BD";
    }
    let feesFormatted = " (" + FormatCurrency(fees, 0) + " " + feesUnit + ")";
    nationality.titleWithFees = nationality.title + feesFormatted;
  }
}
