import {
  Avatar,
  Box,
  Divider,
  Grid,
  Typography,
} from "@material-ui/core";
import IWorkerTwoYear from "../../../interfaces/IWorkerTwoYear";
import FavoriteButton from "../../common/FavoriteButton";
import NiceBox from "../../common/NiceBox";
import OrangeChip from "../../common/OrangeChip";
import SelectWorkerButton from "../../common/SelectWorkerButton";
import IconInfo from "../IconInfo";

const Worker2GridCard = (props: {
  worker: IWorkerTwoYear;
  picClickEvent: any;
}) => {
  const worker = props.worker;

  const clickableStyle: React.CSSProperties = {
    cursor: "pointer",
    userSelect:"none",
  };
  const localTransfer = worker.isLocalTransfer ? (
    <OrangeChip label="Transfer" />
  ) : null;

  const picWidth = "100%";
  const picHeight = 96;

  return (
    <Grid item xs={12} sm={6} md={4}>
      <NiceBox p={1} my={0}>
        <Box style={clickableStyle} onClick={props.picClickEvent}>
          <Box>
            <Typography style={{ fontWeight: "bold" }}>
              {worker.name} {localTransfer}
            </Typography>
          </Box>
          <Box py={1}>
            <Divider />
          </Box>

          <Box>
            <Grid container>
              <Grid item xs={5}>
                <Box textAlign="center" style={clickableStyle}>
                  <Avatar
                    src={worker.smallPicture}
                    style={{
                      width: picWidth,
                      minHeight: picHeight,
                      alignSelf: "center",
                    }}
                    variant="rounded"
                  />
                </Box>
              </Grid>
              <Grid item xs={7}>
                <Box px={1}>
                  <IconInfo icon="work">{worker.jobTitle}</IconInfo>
                  <IconInfo icon="history">{worker.age} years old</IconInfo>
                  <IconInfo icon="brightness_3">{worker.religion}</IconInfo>
                  <IconInfo icon="local_airport">
                    {worker.experienceYears} years experience
                  </IconInfo>
                  <IconInfo icon="attach_money">
                    {worker.monthlySalaryBhdFormatted}
                  </IconInfo>
                  <IconInfo icon="attach_money">
                    {worker.processFeesBhdFormatted}
                  </IconInfo>
                  {/* <IconInfo icon="attach_money">{worker.processFeesBhdFormatted}</IconInfo> */}
                </Box>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Box py={1}>
          <Divider />
        </Box>
        <Box textAlign="center">
          <FavoriteButton />
          <SelectWorkerButton />
        </Box>
      </NiceBox>
    </Grid>
  );
};

export default Worker2GridCard;
