import { Box } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "../../../interfaces/IWorkerTwoYear";
import WorkerTwoYearCardPicTitle from "./Worker2ListCardPicTitle";
import LabelValueLine from "../../common/LabelValueLine";
import LabelValuesContainer from "../../common/LabelValuesContainer";

const WorkerTwoYearDetailsTopSection = (props: { worker: IWorkerTwoYear, showButtons?:boolean }) => {
  const worker = props.worker;

  const oneLine = true;
  const lt = 2;
  const lv = 9;
  const showButtons = props.showButtons !== undefined ? props.showButtons : true;

  return (
    <Box pb={2}>
      <WorkerTwoYearCardPicTitle
        worker={worker}
        picHeight={140}
        showButtons={showButtons}
      >
        <LabelValuesContainer>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Reference #"
          >
            {worker.referenceId}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Job Title"
          >
            {worker.jobTitle}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Processing Fees"
          >
            {worker.processFeesBhdFormatted}
          </LabelValueLine>
          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Salary"
          >
            {worker.monthlySalaryBhdFormatted}
          </LabelValueLine>

          <LabelValueLine
            oneLineTitleSizeMd={lt}
            oneLineValueSizeMd={lv}
            oneLine={oneLine}
            title="Contract Period"
          >
            Two Years
          </LabelValueLine>
        </LabelValuesContainer>
      </WorkerTwoYearCardPicTitle>
    </Box>
  );
};

export default WorkerTwoYearDetailsTopSection;
