import React from 'react';
import { rentalServicesDetailed } from '../../config/FilterOptions';
import { IFilterGroupContainer } from '../../interfaces/IFilterGroupContainer';
import FilterCheckboxItem from './FilterCheckboxItem';
import FilterGroup from './FilterGroup';

const FGService = (props:IFilterGroupContainer) => {
  const expanded = props.isMd ? props.expandedDefaultDesktop : props.expandedDefaultMobile;
  return (
    <FilterGroup title="Lease Duration" expanded={expanded}>
    {rentalServicesDetailed.map((service) => (
      <FilterCheckboxItem key={service.title} title={service.title} />
    ))}
  </FilterGroup>
  );
};

export default FGService;