import { Avatar, Box, Grid } from "@material-ui/core";
import React from "react";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import ConfirmationSummaryItem from "../common/ConfirmationSummaryItem";

const RequestWorkerTwoYear = (props: {
  picWidth: string | number;
  picHeight: string | number;
  worker: IWorkerTwoYear;
}) => {
  const worker = props.worker;
  const picWidth = props.picWidth;
  const picHeight = props.picHeight;

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={4}>
        <Box>
          <Avatar
            style={{
              alignSelf: "center",
              width: picWidth,
              minHeight: picHeight,
            }}
            src={worker.smallPicture}
            variant="rounded"
          />
        </Box>
      </Grid>
      <Grid item xs={12} md={8}>
        <ConfirmationSummaryItem
          title="Agency"
          value={worker.agency?.title}
        />
        <ConfirmationSummaryItem
          title="Reference #"
          value={worker.referenceId}
        />
        <ConfirmationSummaryItem title="Name" value={worker.name} />
        <ConfirmationSummaryItem
          title="Nationality"
          value={worker.nationality.title}
        />
        <ConfirmationSummaryItem
          title="Job Title"
          value={worker.jobTitle}
        />

        <ConfirmationSummaryItem
          title="Salary"
          value={worker.monthlySalaryBhdFormatted}
        />
        <ConfirmationSummaryItem
          title="Processing Fees"
          value={worker.processFeesBhdFormatted}
        />
        <ConfirmationSummaryItem title="Contract Period" value="Two Years" />
      </Grid>
    </Grid>
  );
};

export default RequestWorkerTwoYear;
