import React from "react";
import IAgencyServiceSelectableWorkers from "../../interfaces/IAgencyServiceSelectableWorkers";
import CardChips from "../common/CardChips";

const AgencyTabRentalChips = (props: {
  services: IAgencyServiceSelectableWorkers[];
  showContract: boolean;
  showRental: boolean;
  renderAsText: boolean;
}) => {
  let render: JSX.Element[] | any;
  let services: IAgencyServiceSelectableWorkers[] = [];

  let contractServices = props.services.filter((service) => {
    return service.isTwoYearContract;
  });

  let rentalServices = props.services.filter((service) => {
    return (
      service.isRentalHourly ||
      service.isRentalMonthly ||
      service.isRentalMonthlyStayOver
    );
  });

  if (props.showContract) services.push(...contractServices);
  if (props.showRental) services.push(...rentalServices);

  let chipTitles = services.map((service) => ChipLabel(service));

  if (props.renderAsText) {
    render = <span>{chipTitles.join(", ")}</span>;
  } else {
    render = <CardChips titles={chipTitles} />;
  }
  return <React.Fragment>{render}</React.Fragment>;
};

const ChipLabel = (service: IAgencyServiceSelectableWorkers) => {
  let unit = service.feesBhdUnit ? "/" + service.feesBhdUnit : "";
  let price = service.feesBhd
    ? " (" + service.feesBhd.toString() + " BD" + unit + ")"
    : "";
  return service.title + price;
};

export default AgencyTabRentalChips;
