import * as Factory from "factory.ts";
import Faker from "faker";
import { demoNumberOfRentalWorkersPerServicePerAgency } from "../config/DemoItems";
import IAgencyServiceSelectableWorkers from "../interfaces/IAgencyServiceSelectableWorkers";
import WorkerRentalFactory from "./WorkerRentalFactory";

const AgencyServiceSelectableWorkersFactory = Factory.Sync.makeFactory<IAgencyServiceSelectableWorkers>({
  feesBhd: Factory.each(() => Faker.random.number({ min: 2, max: 5 })),
  // isShowWorkers: true,
  isRentalHourly: false,
  isRentalMonthly: false,
  isRentalMonthlyStayOver: false,
  isTwoYearContract: false,
  requiredDocuments: "",
  title: "UNNAMED",
  workersRental: Factory.each(() =>
    WorkerRentalFactory.buildList(Faker.random.number(demoNumberOfRentalWorkersPerServicePerAgency))
  ),
});

export default AgencyServiceSelectableWorkersFactory;
