import React, { ReactElement } from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import { tWorkerBy } from "../WizardStartRental";

interface Props {
  defaultValue: tWorkerBy,
  onChange: (by: tWorkerBy) => void;
}

export default function WizardRentalByField(props: Props): ReactElement {
  const [value, setValue] = React.useState(props.defaultValue);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    let value = (event.target as HTMLInputElement).value;
    if (value === "hour" || value === "month") {
      setValue(value);
      props.onChange(value);
    }
  };

  return (
    <FormControl component="fieldset">
      <FormLabel component="legend">I want a worker by:</FormLabel>
      <RadioGroup
        row
        aria-label="gender"
        name="gender1"
        value={value}
        onChange={handleChange}
      >
        <FormControlLabel
          value="hour"
          control={<Radio color="primary" />}
          label="Hour"
        />
        <FormControlLabel
          value="month"
          control={<Radio color="primary" />}
          label="Month"
        />
      </RadioGroup>
    </FormControl>
  );
}
